          {row.applicationUserId?.slice(-8) || 'Êú™ÂÖ≥ËÅî'}
        </n-text>
      </div>
    )
  },
  {
    key: 'actions',
    title: 'Êìç‰Ωú',
    align: 'center',
    width: 140,
    render: (row) => (
      <n-button 
        type="primary" 
