];

// Êü•ÁúãÈ™ëÊâãËØ¶ÊÉÖ
async function handleViewDetail(riderId: string) {
  try {
    detailLoading.value = true;
    showDetailModal.value = true;
    riderDetail.value = {};
    riderRanking.value = {};
    
    console.log(`Ëé∑ÂèñÈ™ëÊâãËØ¶ÊÉÖÂíåÁª©ÊïàÊéíÂêçÔºåID: ${riderId}`);
    
    // Âπ∂ÂèëËØ∑Ê±ÇÈ™ëÊâãËØ¶ÊÉÖÂíåÁª©ÊïàÊéíÂêç
    const [detailResponse, rankingResponse] = await Promise.allSettled([
      fetchRiderInfo(riderId),
      fetchRiderPerformanceRanking(riderId, currentYear, currentMonth)
    ]);
    
    // Â§ÑÁêÜÈ™ëÊâãËØ¶ÊÉÖ
    if (detailResponse.status === 'fulfilled' && detailResponse.value?.data) {
      riderDetail.value = detailResponse.value.data;
      console.log('È™ëÊâãËØ¶ÊÉÖ:', riderDetail.value);
    } else {
      console.error('Ëé∑ÂèñÈ™ëÊâãËØ¶ÊÉÖÂ§±Ë¥•:', detailResponse);
    }
    
    // Â§ÑÁêÜÁª©ÊïàÊéíÂêç
    if (rankingResponse.status === 'fulfilled' && rankingResponse.value?.data) {
      riderRanking.value = rankingResponse.value.data;
      console.log('È™ëÊâãÁª©ÊïàÊéíÂêç:', riderRanking.value);
    } else {
      console.error('Ëé∑ÂèñÈ™ëÊâãÁª©ÊïàÊéíÂêçÂ§±Ë¥•:', rankingResponse);
    }
    
  } catch (error) {
    console.error('Ëé∑ÂèñÈ™ëÊâã‰ø°ÊÅØÂ§±Ë¥•:', error);
    message.error('Ëé∑ÂèñÈ™ëÊâã‰ø°ÊÅØÂ§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'));
    showDetailModal.value = false;
  } finally {
    detailLoading.value = false;
  }
}

// Ê≥®ÈáäÔºöÂéü handleViewPerformance ÂáΩÊï∞Â∑≤Âà†Èô§ÔºåÂäüËÉΩÂêàÂπ∂Âà∞ handleViewDetail

// Ê†ºÂºèÂåñÊéíÂêçÈîÆÂêç
function formatRankingKey(key: string) {
  const keyMap = {
    'totalOrdersRanking': 'ÊÄªËÆ¢ÂçïÊéíÂêç',
    'onTimeRateRanking': 'ÂáÜÊó∂ÁéáÊéíÂêç', 
    'goodReviewRateRanking': 'Â•ΩËØÑÁéáÊéíÂêç',
    'incomeRanking': 'Êî∂ÂÖ•ÊéíÂêç',
    'TotalOrdersRanking': 'ÊÄªËÆ¢ÂçïÊéíÂêç',
    'OnTimeRateRanking': 'ÂáÜÊó∂ÁéáÊéíÂêç',
    'GoodReviewRateRanking': 'Â•ΩËØÑÁéáÊéíÂêç',
    'IncomeRanking': 'Êî∂ÂÖ•ÊéíÂêç',
    // Ëã±ÊñáÂ≠óÊÆµÂêçÁøªËØë
    'IncomeRank': 'Êî∂ÂÖ•ÊéíÂêç',
    'OnTimeRateRank': 'ÂáÜÊó∂ÁéáÊéíÂêç',
    'GoodReviewRateRank': 'Â•ΩËØÑÁéáÊéíÂêç',
    'TotalOrdersRank': 'ÊÄªËÆ¢ÂçïÊéíÂêç'
  };
  return keyMap[key] || key;
}

// Ëé∑ÂèñÊéíÂêçÂæΩÁ´†Á±ªÂûã
function getRankingBadgeType(rank: number) {
  if (rank <= 3) return 'success';  // Ââç3Âêç - ÁªøËâ≤
  if (rank <= 10) return 'warning'; // 4-10Âêç - ÈªÑËâ≤
  return 'info';                    // ÂÖ∂‰ªñ - ËìùËâ≤
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂Ëé∑ÂèñÊï∞ÊçÆ
onMounted(() => {
